.newsletter-container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 1200px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem 4rem;
  border-radius: 12px;
  background: linear-gradient(to bottom, #1c1c1c, #9a9a9a);
}

.newsletter-title {
  color: white;
  width: 30%;
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 2rem;
  text-align: center;
}

.newsletter-form {
  width: 40%;
  max-width: 600px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.input-wrapper {
  width: 21rem;
  position: relative;
}

.email-icon {
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  color: #666;
}

.email-input {
  width: 100%;
  padding: 1rem 1rem 1rem 3rem;
  border: none;
  border-radius: 9999px;
  background-color: white;
  color: #333;
  font-size: 1rem;
}

.email-input::placeholder {
  color: #666;
}

.email-input:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.2);
}

.subscribe-button {
  width: 25rem;
  padding: 1rem;
  border: none;
  border-radius: 9999px;
  background-color: white;
  color: #333;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.subscribe-button:hover {
  background-color: #f0f0f0;
}

@media (max-width: 768px) {
  .newsletter-title {
    font-size: 2rem;
  }
}
